@keyframes smooth-appearance {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes smooth-disappearance {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes slide-from-left {
  from {
    transform: translateX(-100%); }
  to {
    transform: translateX(0); } }

@keyframes slide-from-right {
  from {
    transform: translateX(0); }
  to {
    transform: translateX(-100%); } }

