.modal {
  display: none;
  background-color: #ffffff; }
  .modal--active {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    height: 100vh;
    min-height: 768px;
    width: 100%;
    padding: 100px;
    opacity: 1; }
  .modal--inactive {
    opacity: 0;
    animation: smooth-disappearance 1s ease; }
  .modal span[data-logotype] {
    display: block;
    width: 100%;
    min-width: 300px;
    animation: smooth-appearance 3s ease; }
    .modal span[data-logotype] svg {
      display: block;
